/* src/App.css */

/* Reset styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Basic body styling */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

h1, h2 {
  text-align: center;
  margin-bottom: 1rem;
}

/* Link styling */
a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Responsive adjustments for mobile */
@media (max-width: 768px) {
  .container {
    padding: 0.5rem;
  }

  h1, h2 {
    font-size: 1.5rem;
  }
}
